import View from './View.js';
import icons from 'url:../../img/icons.svg';

/**
 * Esta vista controla la acci贸n creada por el bot贸n importar propiedades
 * @property {Element} _parentElement Es el contenedor principal donde se mostrar谩n los pop-up de
 * la importaci贸n de datos
 * @property {String} [_errorMessage='No se pudo realizar la importaci贸n de las propiedades']
 * _errorMessage Es el mensaje que se muestra al renderizar el error
 * @property {String} [_message=''] Define el markup que ser谩 generado si es de carga o mensaje de
 * completado
 * @property {String} [_messageContent=''] Contenido del mensaje que ser谩 mostrado en el pop-up
 * @this {Object} Instancia de la vista
 * @author Carlos Pineda
 * @todo Finalizar implementaci贸n
 */
class importPropertiesView extends View {
    _parentElement = document.querySelector('.popup-container');

    _errorMessage = 'No se pudo realizar la importaci贸n de las propiedades';
    _message = '';
    _messageContent = '';

    /**
     * Se agrega el handler que conecta al controlador con la vista
     * @param {Function} handler Es la funci贸n que se ejecutar谩 al recibir una llamada por el evento
     * @this {Object} Instancia de la vista
     * @author Carlos Pineda
     * @todo Finalizar implementaci贸n
     */
    addHandler(handler) {
        document
            .querySelector('#import-button')
            .addEventListener('click', handler);
    }

    /**
     * Esta funci贸n genera el markup que se renderizar谩 en el pop-up
     * @this {Object} Instancia de la vista
     * @returns {String} Regresa en forma de cadena el html generado
     * @author Carlos Pineda
     * @todo Finalizar implementaci贸n
     */
    _generateMarkup() {
        return `<div class="popup-guardado">
        <div class="popup">
                <div class="popup-inner">
                    ${
                        this._message === 'loading'
                            ? this._generateMarkupLoadingPreview()
                            : this._generateMarkupSuccessPreview()
                    }
                </div>
                
            </div>
        </div>
    </div>`;
    }

    /**
     * Renderiza un error mediante un pop-up
     * @param {String} [message=this._errorMessage] El contenido del mensaje de error
     * @returns {undefined | string} No devuelve ning煤n dato
     * @this {Object} Instancia de la vista
     * @author Carlos Pineda
     * @todo Finalizar implementaci贸n
     */
    renderError(message = this._errorMessage) {
        const markup = `
        <div class="popup-guardado">
        <div class="popup">
                <div class="popup-inner">
                    <div class="alert">
                        <svg>
                            <use href="${icons}#icon-alert-triangle"></use>
                        </svg>
                    </div>
                    <p style="white-space: pre-line">${message}</p>
                </div>
                
            </div>
        </div>
    </div>`;
        this._clear();
        this._parentElement.insertAdjacentHTML('afterbegin', markup);
    }

    /**
     * Esta funci贸n genera el markup de carga que se renderizar谩 en el pop-up
     * @this {Object} Instancia de la vista
     * @returns {String} Regresa en forma de cadena el html generado
     * @author Carlos Pineda
     * @todo Finalizar implementaci贸n
     */
    _generateMarkupLoadingPreview() {
        return `<h3>
        Importando las propiedades
        </h3>
        <div class="spinner">
<svg>
    <use href="${icons}#icon-loader"></use>
</svg>`;
    }

    /**
     * Esta funci贸n genera el markup del mensaje de confirmaci贸n que se renderizar谩 en el pop-up
     * @this {Object} Instancia de la vista
     * @returns {String} Regresa en forma de cadena el html generado
     * @author Carlos Pineda
     * @todo Finalizar implementaci贸n
     */
    _generateMarkupSuccessPreview() {
        return `
                <h3>
                    隆Propiedades importadas correctamente!
                </h3>
                <p>Revisa tu cat谩logo de facebook</p>
                <div class="check">
                <svg>
                    <use href="${icons}#icon-check"></use>
                </svg>
                </div>`;
    }
}

export default new importPropertiesView();
